import Vue from 'vue'
import Router from 'vue-router'
import HelloAsync from '@/components/HelloAsync'
import HelloWorld from '@/components/HelloWorld'
import ChartPage from '@/views/ChartPage'
import TablePage from '@/views/TablePage'
import CollapsePage from '@/views/CollapsePage'
import TabPage from '@/views/TabPage'
import DatePicker from '@/views/DatePicker'
import FullTable from '@/views/FullTable'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'FullTable',
      component: FullTable
    },
    {
      path: '/table',
      name: 'TablePage',
      component: TablePage
    },
    {
      path: '/collapse',
      name: 'CollapsePage',
      component: CollapsePage
    },
    {
      path: '/tab',
      name: 'TabPage',
      component: TabPage
    },
    {
      path: '/date-picker',
      name: 'DatePicker',
      component: DatePicker
    },
    {
      path: '/chart',
      name: 'ChartPage',
      component: ChartPage
    },
    {
      path: '/async',
      name: 'HelloAsync',
      component: HelloAsync
    },
    {
      path: '/world',
      name: 'HelloWorld',
      component: HelloWorld
    }
  ]
})



